.recepie{
    border-radius: 20px;
    box-shadow: 0px 5px 20px rgb(71,71,71);
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background: rgb(213, 206, 206);
    align-items: center;
    width: 30%;
 }
 
 
 @media(max-width:700px){
    .recepie{
       width: 80%;
       text-align: center;
    }
 
 
 }